.MatchPage {
    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-column-gap: 20px;

    .year-selector {
        
        justify-self: end;
        padding: 30px;
    }

    .page-heading {
        margin: 20px;
        text-align: center;
    }

    .no-match {
        text-align: center;
    }

    .link{
        text-align: end;
        margin-bottom: 40px;
        font-size: 20px;
    }

}